import React,{useState} from 'react'

export const ThaaraBalam = () => {
    const nakshatraList=['-','','','','','','','','','','',' ()',' ()','','','','','','','','','','','','','','',''];
    const thaaraList=['','','',' ','',' ',' ',' ',' '];
    const [fromNakshatra,setFromNakshatra]=useState(0);
    const [toNakshatra,setToNakshatra]=useState(0);
    const onChangeFromNakshatraHandler=(e)=>{
        setFromNakshatra(e.target.value);      
        getThaaraBalam();  
      }

      const onChangeToNakshatraHandler=(e)=>{
        setToNakshatra(e.target.value);   
        getThaaraBalam();     
      }

      const getList=(vari,index)=>{
        return (
          <option value={index} key={index}>{vari}</option>
        )
      };

      const getBalam=(from,to)=>{
        let finalCount = -100;
        let difference = to - from;
        if (difference < 0) {
			difference = difference +27+1;	
			finalCount = difference % 9;

		} else if (difference > 0) {
			difference = difference + 1;
			finalCount = difference % 9;
		} else {
			finalCount = 1;
		}
        if (finalCount === 0) {
			finalCount = 9;
		}
        finalCount=finalCount-1;
        return finalCount;
      }

      const getThaaraBalam=()=>{
            const from=fromNakshatra;
            const to=toNakshatra;
            if(from!==0 && to!==0){
                const thaara=getBalam(from,to);
                let color="";
                let thaaraB=thaara+1;
                if((thaaraB%2===0) || (thaaraB===9)){
                    color="text-success";
                } else{
                    color="text-danger";
                }
            
                return <div className="card">
                    <div className="card-header custom-card-body-header">
                    <div className={color}>
                        <center>
                    <h3 className="card-title">
                        {thaaraList[thaara]}
                    </h3>
                    </center>
                    </div>
                    </div>

                    <div className="card-body gita-header">
                        <h4 className="card-text text-primary">{nakshatraList[from]}</h4>
                        <h4 className="card-text text-secondary">{nakshatraList[to]}</h4>
                    </div>
                </div>;


            }else{
                return <div></div>;
            }

      }

  return (
    <div className="container-fluid">
        <div className="dropdown  mb-3 w-100">                                              
            <select className="form-select" aria-labelledby="fromNakshatra" id="fromNakshatra" defaultValue={fromNakshatra} onChange={onChangeFromNakshatraHandler} required>
                {nakshatraList.map((nakshatra,index)=>getList(nakshatra,index))}                    
            </select>
        </div>

        <div className="dropdown  mb-3 w-100">                                              
            <select className="form-select" aria-labelledby="toNakshatra" id="toNakshatra" defaultValue={toNakshatra} onChange={onChangeToNakshatraHandler} required>
                {nakshatraList.map((nakshatra,index)=>getList(nakshatra,index))}                    
            </select>
        </div>

        <div className="container-fluid w-100 p-1 mt-2">
            <div>
                {getThaaraBalam()}
            </div>

        </div>



    </div>
  )
}
