import React, { useState } from 'react';
import Gita from './BhagvadGita.json';
import GitaChapter from './BhagvadGitaChapters.json';
import GitaWithMeaning from './BhagvadGita_Meaning.json';

export const BhagvadGita = () => {
    const[gita,setGita]=useState(Gita);   
    const[filteredGita,setFilteredGita]=useState(Gita);
    const [chapterList,setChapterList]=useState(GitaChapter);
    const [gitaWithMean,setGitaWithMean]=useState(GitaWithMeaning);
    const [selectedChapter,setSelecedChapter]=useState(0);
    const [ch,setCh]=useState(chapterList[0]);

    const ih=(data)=>{
        return {
            __html: data    };
    }

    const getVerse=(verse)=>{             
        var chap=gitaWithMean.filter(chapter=>{
            return (verse.chapter_number==chapter.chapter_number && verse.verse_number==chapter.verse_number);
        });
        var verseMeaning="";
        if(chap.length==1){
            verseMeaning=chap[0].meaning;
        }else{
            console.log("Chapter ",verse.chapter_number, " Verse ", verse.verse_number);
            
        }

        return(
        <div className='card'>
            <div className="card-header">
                {/* <h4 className="card-title text-primary" dangerouslySetInnerHTML={ih(verse.verse_order)}></h4> */}
                <h3 className="card-title text-primary" dangerouslySetInnerHTML={ih(verse.text)}></h3>
                </div>
            <div className="card-body"> 
                <div className="card-text">                   
                    <div className="mb-3">                      
                        <h4 className="card-text text-success" dangerouslySetInnerHTML={ih(verse.transliteration)}></h4>
                    </div>
                    <div className="mb-2">
                        <h5 className="card-text text-secondary" dangerouslySetInnerHTML={ih(verseMeaning)}></h5>
                    </div>
                    <div>                      
                        <h6 className="card-text" dangerouslySetInnerHTML={ih(verse.word_meanings)}></h6>
                    </div>
                  
                </div>
            </div>
      </div> 
        )
    };

    const getChapterList=(chapter,index)=>{
        return (
          <option value={chapter.chapter_number} key={chapter.chapter_number}>{chapter.name_translation} ({chapter.chapter_number})</option>
        )
      };
      const onChangeChapterHandler=(e)=>{
        setSelecedChapter(e.target.value);  

        var chapter=chapterList[e.target.value];              
        setCh(chapter);
        if(e.target.value==0){
            setFilteredGita(Gita);
        }else{            
            setFilteredGita(Gita.filter(chapter=>{
                var targetValue=e.target.value;                             
                return chapter.chapter_id==targetValue;
            }));
                }

        
      };
  
  return (
    <div className="container-fluid mt-3">
        <div className="card">
            <div className="card-header">
                <div>
                    <h3 className="card-title">BhagvadGita</h3>
                </div>
                { <div>
                    <div className="dropdown  mb-3 w-100 mt-3">                 
                        <label className=""></label>                
                         <select className="form-select" aria-labelledby="chapter" onChange={onChangeChapterHandler}>
                            {chapterList.map((chapter,index)=>getChapterList(chapter,index))}                    
                         </select>
                    </div>

                </div> }
            </div>
            <div className="card-header">
                {ch.chapter_number==0 ?
                <div>
                <h4 className="card-title text-danger text-center">
                     {ch.name_translation} ({ch.verses_count} slokas)
                </h4>               
                </div>
                :
                <div>
                <h4 className="card-title text-danger text-center">
                   {ch.chapter_number}. {ch.name_translation} ({ch.verses_count})
                </h4>
                <h6 className="card-title text-primary text-center">
                   ({ch.name_meaning})
                </h6>
                <h5 className="card-title text-secondary">
                    {ch.chapter_summary}
                </h5>
                </div>
                }
            </div>
           
        <div className="card-body">

            {filteredGita.map(verse=>getVerse(verse))}
        </div>
        </div>
    </div>
  )
}
