import React, { useState } from 'react'
import loc from './Location.json'
import Moment from 'moment';
import Clock from 'react-live-clock';
import Geolocation from 'react-native-geolocation-service';
import Alert from 'react-bootstrap/Alert';
import { ToastContainer,toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export const Hora = () => {
    // const locationList=['','','','',''];
   const locationList=['Bantumilli','Vijayawada','Hyderabad','Palakollu','Sringeri'];
    const days=['','','','','','',''];
    const horaList=['Surya','Chandra','Kuja','Budha','Guru','Sukra','Sani'];
    const [loca,setLoca]=useState(loc);
    const [horaDetails, setHoraDetails] = useState([24]);  

    const successMessage=()=>{
      toast.success("Successfully Retrieved the data.");
    }

    var SunCalc = require('suncalc');
    const[date,setDate]=useState(new Date());
    var dat = new Date(date).toISOString().substr(0,10);
    const[latitude,setLatitude]=useState(16.43332);
    const[longitude,setLongitude]=useState(81.00207);
    const[location,setLocation]=useState(0);

    const getTime = (f) =>{
        const d = new Date(f);
        return Moment(d).format('LT');    
      };
    
      const getDate = (f) =>{
        const d = new Date(f);
        return Moment(d).format('MMM DD');    
      };    

    const getList=(vari,index)=>{
        return (
          <option value={index} key={index}>{vari}</option>
        )
      };

      const getLocation =() =>{
        Geolocation.getCurrentPosition((position)=>{
          setLatitude(position.coords.latitude);
          setLongitude(position.coords.longitude);
          setLocation(locationList.length+1);
        },(error)=>{
          console.log(error.code, error.message);
        },{
          enableHighAccuracy:true,timeout:15000,maximumAge:10000
        });
        getHoraDetails();
   
      };

      const onChangeLocationHandler=(e)=>{
        setLocation(e.target.value);      
        const localLoc=loca[e.target.value];
        setLatitude(localLoc.latitude);
        setLongitude(localLoc.longitude);
        getHoraDetails();
          };

          const onChangeDateHandler=(e)=>{
            setDate(e.target.value);
            getHoraDetails();
          };
          const Times=SunCalc.getTimes(new Date(date),latitude,longitude);

          const getHoraNumber=(dayD,i)=>{
            var horaPattern = "05316420531642053164205316420531642";
            horaPattern = horaPattern.replace(dayD,"#");
            let horaArray = horaPattern.split("#");
            let finHora = dayD + horaArray[1];
		    let horaArr = finHora.split("");
            return horaArr[i];
          }

          const getHoraDetails=()=>{
            const lat=latitude;
            const lon=longitude;
            const d=new Date(date);
            horaDetails.splice(0,horaDetails.length);
            const sunTimes=SunCalc.getTimes(new Date(d),lat,lon);
            const sunRise=sunTimes.sunrise;
            const nextDate=new Date(d);       
            nextDate.setDate(new Date(d).getDate()+1);    
            const sunRiseforNextDay=SunCalc.getTimes(new Date(nextDate),lat,lon).sunrise;
            let difference=new Date(sunRiseforNextDay).getTime()-new Date(sunRise).getTime();
            let eachHora=difference/24;
            let halfHora=eachHora/2;
            const dayD=new Date(d).getDay();
            for(var i=1;i<=25;i++){
                let s=new Date(new Date(sunRise).getTime()+((i-1)*eachHora));
                let m=new Date(new Date(s).getTime()+halfHora);
                let e=new Date(new Date(s).getTime()+eachHora); 
                var horaNumber=getHoraNumber(dayD,i-1);
                const h={
                    id:i,
                    hora:horaList[horaNumber],
                    startTime:s,
                    midTime:m,
                    endTime:e,
                    horaResult:"",
                    goodOrBad:""
                };
                horaDetails.push(h);

            }
            

          };


          const getHoraCard=(horaDetails,index)=>{

            const isInBetween=Moment(new Date()).isBetween(Moment(horaDetails.startTime),Moment(horaDetails.endTime));
            let body_Color="";            
            if(isInBetween){
              body_Color=" card-body text-warning";
            }else{
              body_Color=" card-body text-primary";                
            }
            return (
                <div className="card" key={index}>  
                <div className="card-header custom-card-body-header"><h5 className="card-title">{horaDetails.hora} Hora</h5></div>
               <div className={body_Color}>            
                   <div className="custom-card-body card-text">          
                   <div>
                       <p className="card-text">Start</p>                 
                       <p className="card-text">{getTime(horaDetails.startTime)}</p>
                   </div>
                   <div>
                       <p className="card-text">Mid</p>                 
                       <p className="card-text">{getTime(horaDetails.midTime)}</p>
                   </div>
                   <div>
                        <p className="card-text">End</p>                 
                        <p className="card-text">{getTime(horaDetails.endTime)}</p>
                   </div>
                   </div>
               </div>
           </div>
              );


          };


  return (
    <div className="container-fluid">
         <div className="dropdown  mb-3 w-100">                                              
            <select className="form-select" aria-labelledby="location" id="location" defaultValue={location} onChange={onChangeLocationHandler} required>
                {locationList.map((loc,index)=>getList(loc,index))}                    
            </select>
        </div>
        <div className="form-group mb-3">
                  <button className="btn btn-primary w-100 p-3" onClick={getLocation}>Auto Detect</button>
                </div>
        <div className="form-floating mb-3" data-provide="datepicker">
            <input  type="date"  id="date"  className="form-control w-100 p-3"  name="date" defaultValue={dat} required onChange={onChangeDateHandler}/>                  
                <div className="input-group-addon">
                   <span className="glyphicon glyphicon-th"></span>
                </div>
        </div>

        <div className="container-fluid">
            <div className="card">
                 <div className="card-header custom-card-header gita-header">
                    <div className="card-title">
                        <h5 className="">{getDate(date)}</h5>
                        {/* <h6>Sun Rise: {getTime(Times.sunrise)}</h6> */}
                    </div>      
                    <div className="card-title text-primary">
                        <h5><Clock format={'HH:mm:ss'} ticking={true} timezone={'Asia/Kolkata'} /></h5>
                        <h6>{locationList[location]}</h6>
                    </div>              
                    <div className="card-title text-secondary">
                        <h4 className="">{days[new Date(date).getDay()]}</h4>  
                        {/* <h6>Sun Set: {getTime(Times.sunset)}</h6> */}
                    </div>                    
                 </div> 
            </div>
            {getHoraDetails()}
            <div className="">
                {horaDetails.map(hora =>getHoraCard(hora,hora.id))}
            </div>

        </div>


    </div>
  )
}
