import React from "react";
import { Nav,Navbar,NavLink, Container } from "react-bootstrap";
import {Link } from 'react-router-dom';



export const Header = () => {
  return (
    <Navbar collapseOnSelect expand="md" bg="dark" variant="dark" className="p-2">       
        <Navbar.Brand href="/AstroRoops">
          <h1 className="card-title custom_header-logo">
            <span className="text-danger">Astro</span>
            <span className="text-warning">Roops</span>        
          </h1>
          
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="navbarScroll">
        <Nav>
          <NavLink eventKey={1} className="mt-2 hideElement" href="#" data-bs-toggle="modal" data-bs-target="#todaysDetails">Day Details</NavLink>
          <NavLink eventKey={2} className="mt-2 hideElement" href="#" data-bs-toggle="modal" data-bs-target="#sukraSiddantam">Sukra Siddantam</NavLink>
          <NavLink eventKey={3} className="mt-2 hideElement" href="#" data-bs-toggle="modal" data-bs-target="#horaDetails">Hora Details</NavLink>
          <NavLink eventKey={4} className="mt-2" as={Link} to="/AanandhadhiYogam">AnandhadhiYogam</NavLink>
          <NavLink eventKey={5} className="mt-2" as={Link} to="/ThaaraBalam">Thaara Balam</NavLink>
          <NavLink eventKey={6} className="mt-2" as={Link} to="/BhagvadGita">BhagvadGita</NavLink>          
          <NavLink eventKey={7} className="mt-2" as={Link} to="/SukraSiddantam">Sukra Siddantam</NavLink>
          <NavLink eventKey={8} className="mt-2" as={Link} to="/Hora">Hora Details</NavLink>
          <NavLink eventKey={9} className="mt-2" as={Link} to="/Day">Day Details</NavLink>
        </Nav>
      </Navbar.Collapse>        
     </Navbar>
  );

  
};
