import React, { useState } from 'react'
import loc from './Location.json'
import sukra from './SukraSiddantam.json'
import sukraResults from './SukraResults.json'
import Moment from 'moment';
import Clock from 'react-live-clock';
import Geolocation from 'react-native-geolocation-service';


export const SukraSiddantham = () => {
    
    // const locationList=['','','','',''];
   const locationList=['Bantumilli','Vijayawada','Hyderabad','Palakollu','Sringeri'];
    const days=['','','','','','',''];
    const [loca,setLoca]=useState(loc);

    var SunCalc = require('suncalc');
    const[date,setDate]=useState(new Date());
    var dat = new Date(date).toISOString().substr(0,10);
    const[latitude,setLatitude]=useState(16.43332);
    const[longitude,setLongitude]=useState(81.00207);
    const[location,setLocation]=useState(0);
    const [dayLabel,setDayLabel]=useState("Day");
    const [dayTimeGhadiaDetails, setDayTimeGhadiaDetails] = useState([30]);    
    const [nightTimeGhadiaDetails, setNightTimeGhadiaDetails] = useState([30]);    
   
    const onChangeDayHandler=(e)=>{
        if(e.target.checked){
            setDayLabel("Night");   
        }else{
            setDayLabel("Day");
        }
        getSukraSiddantamDetails();
   }

    const getList=(vari,index)=>{
        return (
          <option value={index} key={index}>{vari}</option>
        )
      };

      const getLocation =() =>{
        Geolocation.getCurrentPosition((position)=>{
          setLatitude(position.coords.latitude);
          setLongitude(position.coords.longitude);
          setLocation(locationList.length+1);
        },(error)=>{
          console.log(error.code, error.message);
        },{
          enableHighAccuracy:true,timeout:15000,maximumAge:10000
        });
        
      };

      const onChangeLocationHandler=(e)=>{
        setLocation(e.target.value);      
        const localLoc=loca[e.target.value];
        setLatitude(localLoc.latitude);
        setLongitude(localLoc.longitude);
        getSukraSiddantamDetails();
        
          }

          const onChangeDateHandler=(e)=>{
            setDate(e.target.value);
            getSukraSiddantamDetails();
          }

          const getHoraDetails=(startTime,difference)=>{

            let eachGhadia=difference/30;
            let halfGhadia=eachGhadia/2;
            
            const res=sukra[new Date(date).getDay()];                       
            const res1=sukraResults[new Date(date).getDay()];
            const totalGhadias=[30];
            
            for(var i=1;i<=30;i++){
                let s=new Date(new Date(startTime).getTime()+((i-1)*eachGhadia));
                let m=new Date(new Date(s).getTime()+halfGhadia);
                let e=new Date(new Date(s).getTime()+eachGhadia);     
                 
                const gh={
                    ghadiaId:i,
                    ghadiaStartTime:s,
                    ghadiaMidTime:m,
                    ghadiaEndTime:e,
                    ghadiaResult:res[i],
                    goodOrBad:res1[i]
                };
                
                
                totalGhadias.push(gh)
            }           
            return totalGhadias;

          }


          const getSukraSiddantamDetails=()=>{
            const lat=latitude;
            const lon=longitude;
            const d=date;

            dayTimeGhadiaDetails.splice(0,dayTimeGhadiaDetails.length);
            nightTimeGhadiaDetails.splice(0,nightTimeGhadiaDetails.length);
            const nextDate=new Date(d);       
            nextDate.setDate(nextDate.getDate()+1);    
            const sunTimes=SunCalc.getTimes(new Date(d),lat,lon);
            const sunRiseforNextDay=SunCalc.getTimes(nextDate,lat,lon).sunrise;

            const sunRise=sunTimes.sunrise;
            const sunSet=sunTimes.sunset;
            
            let horas=[];
                
            if(sunRise!=="Invalid Date" && sunSet!=="Invalid Date"){            
                if(dayLabel==="Day"){
                    const dayTime=sunSet.getTime()-sunRise.getTime();
                    horas=getHoraDetails(sunRise,dayTime);   
                    for(var i=0;(i<horas.length);i++){
                        dayTimeGhadiaDetails.push(horas[i]);
                    }                 
                }else{
                    const nightTime=sunRiseforNextDay.getTime()-sunSet.getTime();
                    horas=getHoraDetails(sunSet,nightTime);
                    for(var i=0;(i<horas.length);i++){
                        nightTimeGhadiaDetails.push(horas[i]);
                    }    
                }
                
                
            }

           
         

          }

          const getGhadiaDetails=(ghadia,index)=>{
            if(index==undefined){
                return "";
               }else{         
                var o=ghadia.goodOrBad;
                var header_color="";
                var body_Color="";          
                
                const isInBetween=Moment(new Date()).isBetween(Moment(ghadia.ghadiaStartTime),Moment(ghadia.ghadiaEndTime));
                
    
                if(o=="GOOD"){
                    header_color="card-title text-success";
                    body_Color="card-text";
                }else{
                    header_color="card-title text-danger";
                    body_Color=" card-text";
                }
                if(isInBetween){
                    body_Color=" card-text text-warning";
                }else{
                    body_Color=" card-text text-primary";                
                }
    
                return (
                  <div className="card col-12" key={index}>  
                  <div className="card-header custom-card-body-header"><h5 className={header_color}>{ghadia.ghadiaResult}</h5></div>
                 <div className="card-body">            
                     <div className="custom-card-body card-text">          
                     <div>                                
                         <p className={body_Color}>{getTime(ghadia.ghadiaStartTime)}</p>
                     </div>
                     <div>                                    
                         <p className={body_Color}>{getTime(ghadia.ghadiaMidTime)}</p>
                     </div>
                     <div>                                 
                          <p className={body_Color}>{getTime(ghadia.ghadiaEndTime)}</p>
                     </div>
                     </div>
                 </div>
             </div>
                );
               }
              }
              const getTime = (f) =>{
                const d = new Date(f);
                return Moment(d).format('LT');    
              };
            
              const getDate = (f) =>{
                const d = new Date(f);
                return Moment(d).format('MMM DD');    
              };    

        const Times=SunCalc.getTimes(new Date(date),latitude,longitude);
              

    
  return (
    <div className="container-fluid">
         <div className="dropdown  mb-3 w-100">                                              
            <select className="form-select" aria-labelledby="location" id="location" defaultValue={location} onChange={onChangeLocationHandler} required>
                {locationList.map((loc,index)=>getList(loc,index))}                    
            </select>
        </div>
        <div className="form-group mb-3">
                  <button className="btn btn-primary w-100 p-3" onClick={getLocation}>Auto Detect</button>
                </div>
        <div className="form-floating mb-3" data-provide="datepicker">
            <input  type="date"  id="date"  className="form-control w-100 p-3"  name="date" defaultValue={dat} required onChange={onChangeDateHandler}/>                  
                <div className="input-group-addon">
                   <span className="glyphicon glyphicon-th"></span>
                </div>
        </div>

        <div className="container-fluid">

        <div className="form-check form-switch m-3">
            <input className="form-check-input" type="checkbox" id="flexSwitchCheckChecked" onChange={onChangeDayHandler}/>
            <label className="form-check-label">{dayLabel}</label>
        </div>

            <div className="card">
                 <div className="card-header custom-card-header gita-header">
                    <div className="card-title">
                        <h5 className="">{getDate(date)}</h5>
                        {/* <h6>Sun Rise: {getTime(Times.sunrise)}</h6> */}
                    </div>      
                    <div className="card-title text-primary">
                        <h5><Clock format={'HH:mm:ss'} ticking={true} timezone={'Asia/Kolkata'} /></h5>
                        <h6>{locationList[location]}</h6>
                    </div>              
                    <div className="card-title text-secondary">
                        <h4 className="">{days[new Date(date).getDay()]}</h4>  
                        {/* <h6>Sun Set: {getTime(Times.sunset)}</h6> */}
                    </div>
                    
                 </div>
                 {getSukraSiddantamDetails()}
                        
                {dayLabel === 'Day' ? 
                <div id="dayTimeDetails" className="">             
                    {dayTimeGhadiaDetails.map(day =>getGhadiaDetails(day,day.ghadiaId))}
                </div>
                    : 
                <div id="nightTimeDetails" className="">             
                    {nightTimeGhadiaDetails.map(day =>getGhadiaDetails(day,day.ghadiaId))}
                </div>
                }
            </div>

        </div>


    </div>
  )
}
