import React, { useState } from 'react';
import AnandhiYogam from './AanadhadhiYogam.json';


export const AnnadhiYoga = () => {
    const[yogam,setYogam]=useState(AnandhiYogam);
    //const nakshatraList=['Select Nakshatra','Ashwini','Bharani','Krittika','Rohini','Mrigashira','Ardra','Punarvasu','Pushyami','Ashlesha','Magha','Purva Phalguni(Pubba)','Uttara Phalguni(Uttara)','Hastha','Chitta','Swati','Vishakha','Anuradha','Jyeshtha','Moola','Purvashadha','Uttarashadha','Shravana','Abhijith','Dhanishtha','Shatabhisha','Purvabhadrapada','Uttara Bhadra','Revati'];
    const nakshatraList=['-','','','','','','','','','','',' ()',' ()','','','','','','','','','','','','','','','',''];
    const days=['','','','','','',''];
    const [nakshatra,setNakshatra]=useState(0);
    const [day,setDay]=useState(new Date().getDay());

    const getList=(vari,index)=>{
        return (
          <option value={index} key={index}>{vari}</option>
        )
      };
      

      const onChangeNakshatraHandler=(e)=>{
        setNakshatra(e.target.value);
        getAllYogams();
      }
      const onChangeDayHandler=(e)=>{
        setDay(e.target.value);
        getAllYogams();
      }

      const getYogam=(selectedDay,naksh)=>{
        const total=28;
        const mul=selectedDay*4;

        var loc=parseInt(total)-parseInt(mul)+parseInt(naksh);         
        if(loc>total){
            loc=loc-total;
        }
        
        return yogam[parseInt(loc-1)];
      }


      const getAllYogams=()=>{
        const d=day;
        const n=nakshatra;
        if(n==0){            
            var temp="";
            const mul=d*4;   
            const newList=[];         
                for(var i=parseInt(mul);i<28;i++){
                    newList.push(parseInt(i));
                }
                 for(var j=0;j<parseInt(mul);j++){
                    newList.push(parseInt(j));
                 }
                 
                 newList.map(l=>{getYogamCard(getYogam(parseInt(d),parseInt(l)),parseInt(l))});
                 

        }else{
            return getYogamCard(getYogam(d,n),n);
        }


      }

      const getYogamCard=(y,n)=>{
        let colo="";
        //console.log(y);
        if((y.resultCode)>0){
            colo="text-success";
        }else{
            colo="text-danger";
        }
        const ds=y.days;
       return  <div className="card">
            <div className="card-header gita-header custom-card-body-header">
                <div className={colo}>
                     <h3 className="card-title">
                        {y.yogam} 
                    </h3>
                    
                    {ds>1 ? <h5 className="">{ds} </h5> : <h5 className="">{ds} </h5>}

                    
                </div>
                <div className={colo}>
                     <h3 className="card-title">
                        {nakshatraList[n]}
                    </h3>
                    <h5 className="">
                    {days[day]}
                    </h5>
                </div>
            </div>
            <div className="card-body">
                <h4 className="card-text text-primary">
                    {y.results}
                </h4>
            </div>
        </div>;
      }
      


  return (
    <div>
        <div className="container-fluid">
             <div className="dropdown  mb-3 w-100">                                              
              <select className="form-select" aria-labelledby="nakshatra" id="nakshatra" defaultValue={nakshatra} onChange={onChangeNakshatraHandler} required>
                    {nakshatraList.map((nakshatra,index)=>getList(nakshatra,index))}                    
                  </select>
              </div>

              <div className="dropdown  mb-3 w-100">                                              
              <select className="form-select" aria-labelledby="days" id="selectedDay" defaultValue={day} onChange={onChangeDayHandler} required>
                    {days.map((day,index)=>getList(day,index))}                    
                  </select>
              </div>
              
        </div>
        <div className="container-fluid w-100 p-1 mt-2">            
            <div>{getAllYogams()}</div>
        </div>
        <div className="container-fluid w-100 p-1 mt-2">            
        <div className="card">
            <div className="card-header gita-header custom-card-body-header">
                <div className="text-danger">
                     <h3 className="card-title">
                      
                                         </h3>
                    
                </div>               
            </div>
            <div className="card-body text-secondary">
            <h4 className="card-text">
            +,
                </h4>
                <h4 className="card-text">
                +,
                </h4>
                 <h4 className="card-text">
                 +,
                </h4>
                <h4 className="card-text">
                +,
                </h4>                
                <h4 className="card-text">
                +,
                </h4>
                <h4 className="card-text">
                +,
                </h4>
                <h4 className="card-text">
                +.
                </h4>
            </div>
        </div>

        </div>


    </div>
  )
}
